import { ItemMetadata } from '../utils/UNSAFE_dataProvider';

export const variants = ['banner', 'inline', 'toast'] as const;
export type MessageVariant = typeof variants[number];

export const severities = ['error', 'warning', 'confirmation', 'info', 'none'] as const;
export type MessageSeverity = typeof severities[number];

/**
 * Structure of data item passed to the renderer functions
 */
export type MessageRendererDataItem<K, D> = {
  /**
   * The data for the current message
   */
  data: D;

  /**
   * The key for the current message
   */
  key: K;

  /**
   * The metadata for the current message
   */
  metadata?: ItemMetadata<K>;
};
